<template>
  <li class="screen">
    <div
      class="screen-bg"
      :class="[{ 'is-loading': loading }, animClass]"
      v-if="media"
    >
      <img :src="imgSrc" v-if="type_img" />
      <iframe
        v-else-if="type_video"
        class="screen-bg__video"
        style="height: 34vmax"
        :src="video_src"
        frameborder="0"
        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
      ></iframe>
    </div>
    <div
      v-if="msgOut"
      class="msg-box msg-question slide-right"
      :style="customize"
      :key="screen.id"
    >
      <div class="msg-inner" v-html="msgOut"></div>
    </div>
    <div class="btn-group" v-if="buttons">
      <v-button
        v-for="(button, index) of buttons"
        :key="index"
        class="slide-top"
        :style="[button.customize, { animationDelay: 120 * index + 'ms' }]"
        @click="next(screen, button)"
        :disabled="isClicked"
        v-html="tagReplacer(button.text)"
      >
      </v-button>
    </div>
    <div class="msg-box msg-answer slide-left" v-if="screen.answer">
      <div class="msg-answer">
        <div class="msg-inner" v-html="screen.answer"></div>
      </div>
    </div>
  </li>
</template>

<script>
import VButton from "../VButton";
import { mapActions } from "vuex";

export default {
  components: {
    VButton
  },
  props: {
    screen: Object,
    index: Number
  },
  data() {
    return {
      imgSrc: null,
      loading: false,
      answer: null
    };
  },
  mounted() {
    this.imgLoading();
  },
  computed: {
    isClicked() {
      return this.$store.state.loading || this.screen.clicked;
    },
    media() {
      if (this.screen.attacment) return true;
      else return false;
    },
    video_src() {
      if (this.screen.attacment.type === "youtube") {
        return `https://www.youtube.com/embed/${this.screen.attacment.data}`;
      }
      if (this.screen.attacment.type === "vimeo") {
        return `https://player.vimeo.com/video/${this.screen.attacment.data}`;
      }
    },
    buttons() {
      if (!this.screen.buttons.length) return false;
      return this.screen.buttons.map(btn => {
        return Object.assign(btn, {
          customize: {
            background: `#${btn.customize.background}`,
            color: `#${btn.customize.color}`
          }
        });
      });
    },
    customize() {
      return {
        background: `#${this.screen.customize.background}`,
        color: `#${this.screen.customize.color}`
      };
    },
    type_img() {
      if (!this.screen.attacment) return false;
      else if (this.screen.attacment.type === "image") {
        return true;
      } else return false;
    },
    type_video() {
      if (!this.screen.attacment) return false;
      else if (this.screen.attacment.type === "vimeo") {
        return true;
      } else return false;
    },
    msgOut() {
      let text = this.screen.text;
      text = text.replace(/\n/gm, "<br>");
      return this.tagReplacer(text);
    },
    animClass() {
      if (this.index === 0) {
        return "appear-tada";
      } else return "appear";
    }
  },
  methods: {
    ...mapActions(["setAnswer", "nextMessage", "scroll", "setVariable"]),
    tagReplacer(text) {
      if (!text) return;
      if (text.includes("[bold]")) {
        text = text
          .replace("[bold]", "<strong>")
          .replace("[/bold]", "</strong>");
      }
      if (text.includes("[em]")) {
        text = text.replace("[em]", "<em>").replace("[/em]", "</em>");
      }
      return text;
    },
    next(screen, button) {
      let action = button.action,
        anchor = button[action],
        answer = this.tagReplacer(button.text);
      if (action === "link") {
        window.open(anchor);
      }
      let variable = {
        name: button.variable.name,
        value: button.variable.value
      };
      this.nextMessage([screen, variable, answer, anchor, action]);
      screen.clicked = true;
    },
    imgLoading() {
      if (!this.screen.attacment) return false;
      this.loading = true;
      this.imgSrc = this.screen.attacment.thumbnail;
      setTimeout(() => {
        this.imgSrc = this.screen.attacment.data;
        this.loading = false;
      }, 400);
    }
  }
};
</script>

<style lang="scss">
.screen {
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;
  &:last-child {
    margin-bottom: 0;
  }
}
.screen-bg {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-height: 100%;
  overflow: hidden;
  img {
    width: 100%;
  }
  &__video {
    width: 100%;
    .vp-video {
      background: #000;
    }
  }
  .loading {
    filter: blur(8px);
  }
}

.btn-group {
  margin: 20px 0;
  .screen-btn {
    opacity: 0;
    transform: translateY(50%);
  }
}

.msg-container {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.msg-box {
  position: relative;
  border-radius: 7px;
  padding: 10px 5px 10px 15px;
  line-height: 1.5;
  transition: 0.3s;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    width: 54px;
    height: 13px;
    background: inherit;
    /*border: 13px solid transparent;*/
  }
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    z-index: 10;
    height: 20px;
    width: 20px;
    background: #000;
  }
  &.msg-question {
    max-width: 45%;
    margin-top: 15px;
    .msg-inner {
      padding: 7px 20px 7px 0;
      p {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &:before {
      left: -13px;
      transform: translate(-80%, 62%) rotate(-45deg);
      transform-origin: 0 0;
    }
    &:after {
      left: -13px;
    }
  }
  &.msg-answer {
    display: inline-flex;
    min-width: 100px;
    max-width: 60%;
    padding: 10px 20px;
    background: #cfe2fa;
    color: #000;
    margin: 20px 0 20px auto;

    &:before {
      right: -13px;
      transform: translate(41%, 62%) rotate(-45deg);
      transform-origin: 0 0;
    }
    &:after {
      right: -13px;
    }
  }
  .msg-inner {
    word-break: break-word;
    position: relative;
    z-index: 10;
  }
  @media only screen and (max-width: 640px) {
    .msg-inner {
      font-size: 12px;
    }
    &.msg-answer {
      max-width: 90%;
    }
    &.msg-question {
      max-width: 80%;
    }
  }
}
</style>
