<template>
  <div class="screen-list-container">
    <div class="container-scrollable">
      <ul class="screen-list">
        <screen-item
          v-for="(screen, index) of SCREENS"
          :screen="screen"
          :index="index"
          :key="index"
        />
      </ul>
    </div>
    <v-input />
    <v-loader/>
  </div>
</template>

<script>
import ScreenItem from "./ScreenItem";
import VInput from "../VInput";
import VLoader from "../VLoader"
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ScreenList",
  components: {
    ScreenItem,
    VInput,
    VLoader
  },
  data() {
    return {};
  },
  mounted() {
    this.DOWNLOAD_SCREEN_FROM_API([]);
  },
  computed: {
    ...mapGetters(["SCREENS"])
  },
  methods: {
    ...mapActions(["DOWNLOAD_SCREEN_FROM_API", "nextMessage"])
  }
};
</script>

<style lang="scss">
.container-scrollable {
  overflow-x: hidden;
  overflow-y: scroll;
  height: calc(100vh - 62px);
  &::-webkit-scrollbar {
    background: #f0f0f0;
    width: 10px;
    box-shadow: inset 0 0 5px 5px rgba(#000, 0.5);
    transition: 0.3s;
    &:hover {
      width: 15px;
    }
  }

  &::-webkit-scrollbar-track {
    background-color: #f0f0f0;
    box-shadow: inset 0 0 7px 2px rgba(0, 0, 0, 0.5);
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 20px;
    width: 10px;
    background: linear-gradient(left, rgba(#000, 0.2), rgba(#000, 0.6));
    box-shadow: inset 0 0 1px 1px #5c6670;
    transition: 0.3s;
    &:hover {
      background: linear-gradient(left, rgba(#000, 0.5), rgba(#000, 0.8));
    }
  }
}

.screen-list {
  padding: 0 20px 30px;
  @media only screen and (max-width: 640px) {
    .screen-list {
      padding: 0 16px 10px;
    }
  }
}
</style>
