<template>
  <button class="screen-btn" @click="$emit('click', $event)" type="button">
    <span>
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {};
</script>

<style lang="scss">
.screen-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  padding: 5px;
  background: none;
  border: none;
  border-radius: 8px;
  min-height: 48px;
  color: #fff;
  background: rgb(36, 99, 118);
  width: 100%;
  margin-bottom: 10px;
  overflow: hidden;
  transition: 0.3s;
  span {
    position: relative;
    z-index: 10;
  }
  &:last-child {
    margin-bottom: 0;
  }
  &:active {
    transform: scale(0.98);
  }
  &:before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(0, 0, 0, 1);
    transition: 0.3s ease-out;
  }
  &:hover,
  &:focus {
    &:before {
      opacity: 0.3;
    }
  }
  &:disabled {
    &:focus,
    &:hover {
      cursor: not-allowed;
      &:before {
        opacity: 0;
      }
    }
  }
  @media only screen and (max-width: 640px) {
    font-size: 12px;
    min-height: 40px;
  }
}
</style>
