import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    lockInput: true,
    placeholder: "Ваше сообщение",
    loading: false,
    screens: [],
    anchor_global: "",
    user_id: "777-000987",
    // method: "main",
    anchor: "cinema",
    action: "",
    role: ""
  },
  mutations: {
    // Записать название и значение переменной в STATE
    SET_VARIABLE(state, variable) {
      state[variable.name] = variable.value;
      state.variable = variable.name;
    },
    // Записать название и значение якоря в STATE
    NEXT_SCREEN_URL(state, [anchor, action]) {
      // action может быть "anchor" или "anchor_global"
      // anchor - ссылка на следующую страницу (id)
      state[action] = anchor;
      state.action = action;
      if (action === "anchor") {
        state.anchor = anchor;
      } else if (action === "anchor_global") {
        state.anchor_global = anchor;
        state.role = state.anchor_global;
      }
    },
    // Записать ссылку на следующую страницу
    SET_ANCHOR: (state, id) => (state.anchor = id),
    // Загрузить в STATE новую страницу (screen)
    SET_SCREEN_TO_STATE(state, screen) {
      // screen.visible = false;
      if (state.screens.length < 1) {
        state.screens.push(screen);
      } else if (!state.screens.includes(screen)) {
        state.screens.push(screen);
      }
    },
    LOCK_INPUT(state, value) {
      if (state.loading) {
        state.lockInput = true;
      }
      state.lockInput = value;
    },
    SET_ANSWER(state, [index, answer]) {
      Vue.set(state.screens[index], "answer", answer);
    },
    LOADER_SHOW(state) {
      state.loading = true;
    },
    LOADER_HIDE(state) {
      state.loading = false;
    }
  },
  actions: {
    setAnswer({ commit, dispatch, state }, [screen, answer]) {
      state.screens.forEach((msg, index) => {
        if (msg.id === screen.id) {
          commit("SET_ANSWER", [index, answer]);
          dispatch("scroll");
        }
      });
    },
    nextMessage({ commit, dispatch }, data = []) {
      let [screen, variable, answer, anchor, action] = data;
      commit("NEXT_SCREEN_URL", [anchor, action]);

      dispatch("setAnswer", [screen, answer]);
      setTimeout(() => commit("LOADER_SHOW"), 600);
      setTimeout(
        () => dispatch("DOWNLOAD_SCREEN_FROM_API", [anchor, action]),
        1000
      );
      if (variable.name) {
        commit("SET_VARIABLE", variable);
        commit("LOCK_INPUT", true);
      }
    },
    DOWNLOAD_SCREEN_FROM_API(
      { commit, dispatch, state },
      [anchor = state.anchor, action = "anchor"]
    ) {
      commit("LOADER_SHOW");

      commit("NEXT_SCREEN_URL", [anchor, action]);
      let url = `https://crm-pro.ru/new/sbc/type/vk/get_data.php?method=main&id=${state.user_id}&p=${state[action]}`;
      if (state.role && state.action === "anchor") {
        // если выбран пол и ссылка на id страницы
        url = `https://crm-pro.ru/new/sbc/type/vk/get_data.php?method=page&id=${state.user_id}&p=${state.role}&l=${anchor}`;
      }

      return axios(url, {
        method: "GET"
      })
        .then(response => {
          // Если данные не объект, не выполнять код.
          if (!(response.data instanceof Object)) return;
          let screen = response.data;
          console.log(response);
          // добавить загруженному объекту свойство answer (ответ)
          Vue.set(screen, "answer", null);
          // разблокировать доступ к текстовому полю
          if (screen.variable.name) {
            commit("LOCK_INPUT", false);
          }
          // если в тексте страницы используется переменная
          // тогда меняем ее на значение сохраненное в STATE
          if (screen.text.includes(state.variable)) {
            screen.text = screen.text.replace(
              state.variable,
              state[state.variable]
            );
          }
          commit("SET_SCREEN_TO_STATE", screen);
          return screen;
        })
        .catch(() => commit("LOCK_INPUT", true))
        .finally(() => {
          commit("LOADER_HIDE");
          dispatch("scroll");
        });
    },
    scroll() {
      let container = document.querySelector(".container-scrollable"),
        list = document.querySelector(".screen-list");
      setTimeout(() => (container.scrollTop = list.scrollHeight), 50);
    }
  },
  getters: {
    SCREENS: state => state.screens,
    SCREEN_WITH_VARIABLE(state) {
      let screen;
      state.screens.find(item => {
        if (item.variable.name) {
          screen = item;
        }
      });
      return screen;
    },
    LOADING: state => state.loading,
    LOCK: state => state.lockInput
  }
});