            <path
              d="M256,0C114.848,0,0,114.848,0,256s114.848,256,256,256s256-114.848,256-256S397.152,0,256,0z M256,480
			C132.512,480,32,379.52,32,256S132.512,32,256,32s224,100.48,224,224S379.488,480,256,480z"
            />
