              d="M160,128c-35.296,0-64,28.704-64,64c0,8.832,7.168,16,16,16c8.832,0,16-7.168,16-16c0-17.632,14.336-32,32-32
			s32,14.368,32,32c0,8.832,7.168,16,16,16c8.832,0,16-7.168,16-16C224,156.704,195.296,128,160,128z"
            />
          </g>
        </g>
        <g>
          <g>
            <path
              d="M438.016,294.336c-3.008-4-7.744-6.336-12.736-6.336H86.72c-4.992,0-9.728,2.336-12.736,6.336
			c-3.04,4-4,9.184-2.656,14.016C94.464,390.56,170.4,448,256,448s161.536-57.44,184.672-139.648
			C442.016,303.52,441.056,298.336,438.016,294.336z M256,416c-64.032,0-121.568-38.56-146.624-96h293.248
			C377.568,377.44,320.032,416,256,416z"
