      </svg>
    </div>
    <div class="msg-input__field">
      <input
        type="text"
        v-model="value"
        placeholder="Введите сообщение"
        ref="input"
        :disabled="LOCK"
      />
    </div>
    <button class="msg-input__btn" @click="sendValue($event)" :disabled="LOCK">
      <svg
        id="Layer_1"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 24 24"
        width="24"
        height="24"
      >
        <path
          fill="#3b85f5"
          fill-opacity="1"
          d="M1.101 21.757L23.8 12.028 1.101 2.3l.011 7.912 13.623 1.816-13.623 1.817-.011 7.912z"
        ></path>
      </svg>
    </button>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  props: {
    screenVariable: Object
  },
  data() {
    return {
      value: null,
    };
  },
  watch: {
    LOCK() {
      if (document.documentElement.clientHeight < 768) return;
      if (!this.LOCK) {
        setTimeout(() => this.$refs.input.focus(), 200);
      }
    }
  },
  computed: {
    ...mapGetters(["LOCK", "SCREEN_WITH_VARIABLE"])
  },
  methods: {
    ...mapActions(["nextMessage"]),
    sendValue(event) {
      if (
        event.type === "click" ||
        (event.type === "keypress" && event.keyCode === 13)
      ) {
        if (!this.value) return alert("Введите сообщение");
        if (this.value) {
          let screen = this.SCREEN_WITH_VARIABLE,
            answer = this.value,
            anchor = screen.variable.next,
            action = "anchor";

          let variable = {
            name: screen.variable.name,
            value: this.value
          };

          this.nextMessage([screen, variable, answer, anchor, action]);
          this.value = "";
        }
      }
    }
  }
};
</script>

<style lang="scss">
.msg-input {
  position: relative;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px 10px 20px;
  background: #f0f0f0;
  &__field {
    width: 100%;
    input {
      padding: 6px 10px 6px 20px;
      height: 42px;
      width: 95%;
      border-radius: 21px;
      background: #fff;
      border: 1px solid transparent;
      transition: 0.3s;
      font-size: 16px;
      box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
      &:focus {
        box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5),
          inset 0 0 4px 1px rgba(0, 0, 0, 0.5);
      }
      &:disabled {
        cursor: not-allowed;
        box-shadow: none;
      }
      &::placeholder {
        color: #999;
        font-size: 16px;
      }
    }
  }
  &__btn {
    padding: 5px 10px;
    background: none;
    border: none;
    &:disabled {
      cursor: not-allowed;
    }
  }
}
.msg-icon-smile {
  width: 30px;
  margin-right: 10px;
  cursor: pointer;
  svg {
    transition: 0.3s;
    fill: #999;
  }
  &:hover svg {
    fill: darken(#999, 30%);
  }
}
</style>
